(function() {
    'use strict';

    angular
        .module('app', [
            // common
            'app.core',

            // features
            'app.calendar'
        ]);
})();

(function() {
    'use strict';

    angular
        .module('app')
        .config(router);

    router.$inject = ['$urlRouterProvider', '$stateProvider'];

    function router($urlRouterProvider, $stateProvider) {
        $stateProvider
            .state('calendar', {
                url: '/calendar',
                templateUrl: 'app/calendar/calendar.html',
                controller: 'CalendarController',
                controllerAs: 'vm'
            });
    }
})();

(function() {
    'use strict';

    angular
        .module('app.calendar', [
            'app'
        ]);

})();

(function() {
    'use strict';

    angular
        .module('app.calendar')
        .controller('CalendarController', CalendarController);

    CalendarController.$inject = [];

    /* @ngInject */
    function CalendarController() {
        var vm = this;

        activate();

        function activate() {
            vm.date = 'Февраль 2016'
        }
    }
})();

(function() {
    'use strict';

    angular
        .module('app')
        .config(router);

    router.$inject = ['$urlRouterProvider'];

    function router($urlRouterProvider) {
        $urlRouterProvider.otherwise('/calendar');
    }
})();

(function() {
    'use strict';

    angular
        .module('app')
        .constant('CONFIG', config);

    var config = {
        API_ROOT: 'http://demo9752422.mockable.io/'
    };

})();

(function() {
    'use strict';

    angular
        .module('app.core', [
            'ngAnimate',
            'ngSanitize',
            'ngMessages',
            'ui.router'
        ]);

})();

angular.module("webapp").run(["$templateCache", function($templateCache) {$templateCache.put("app/calendar/calendar.html","<div><div class=\"l-header ui-clearfix\"><div class=\"l-header__left\"><div class=\"l-header__block\"><div class=\"b-calendar-heading\"><h2 class=\"ui-h1\" ng-bind=\"vm.date\"></h2></div></div><div class=\"l-header__block\"><div class=\"b-calendar-filter\"></div></div></div><div class=\"l-header__right\"><div class=\"l-header__block\"></div><div class=\"l-header__block\"></div></div></div><div class=\"l-content\"></div></div>");}]);